/*******************************************************************************
 * Style overrides for distill blog posts.
 ******************************************************************************/

d-byline {
  border-top-color: var(--global-divider-color) !important;
}

d-byline h3 {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.6rem !important;
}

d-byline span {
  color: rgba(0, 0, 0, 1.0);
}

d-byline a, d-article d-byline a {
  color: var(--global-text-color) !important;
  &:hover {
    color: var(--global-hover-color) !important;
  }
}

d-byline .authors-affiliations {
  grid-column-end: span 3 !important;
  grid-template-columns: 1fr 2fr;
}

d-article {
  border-top-color: var(--global-divider-color) !important;
  a, p, h1, h2, h3, h4, h5, h6, li, table {
    color: var(--global-text-color);
  }
  a, h1, h2, hr, table, table th, table td {
    border-bottom-color: var(--global-divider-color) !important;
  }
  a {
    border-bottom-color: rgba(0, 0, 0, 0.7) !important;
  }
  a:hover {
    border-bottom-color: var(--global-hover-color) !important;
  }
  b i {
    display: inline;
  }

  d-contents {
    width: 100%;
    align-self: start;
    grid-column: 1 / 4;
    grid-row: auto / span 4;
    justify-self: start;
    margin-top: 0em;
    padding-left: 2em;
    //border-right: 1px solid var(--global-divider-color);
    padding-right: 1em;
    margin-right: 0px;
    margin-top:  0em;

    nav {
      grid-column: toc !important;
      a {
        border-bottom: none !important;
        &:hover {
          border-bottom: 1px solid var(--global-text-color) !important;
        }
      }
      h3 {
        margin-top: 0;
        margin-bottom: 1em;
        color: rgba(0, 0, 0, 0.7) !important;
      }
      div {
        display: block;
        outline: none;
        margin-bottom: 0.8em;
        color: rgba(0, 0, 0, 0.8);
        font-weight: bold;
      }
      ul {
        padding-left: 1em;
        margin-top: 0;
        margin-bottom: 6px;
        list-style-type: none;
        li {
          margin-bottom: 0.25em;
        }
      }
    }
    .figcaption {
      line-height: 1.4em;
    }
    toc-line {
      border-right: 1px solid var(--global-divider-color);
      grid-column: toc-line;
    }
  }

  table caption {
    margin-top: 5px;
    caption-side: bottom;
    line-height: 1.4em;
  }

  d-footnote {
    scroll-margin-top: 66px;
  }
}

d-appendix {
  border-top-color: var(--global-divider-color) !important;
  color: var(--global-distill-app-color) !important;
  h3, li, span {
    color: var(--global-distill-app-color) !important;
  }
  a, a.footnote-backlink {
    color: var(--global-distill-app-color) !important;
    &:hover {
      color: var(--global-hover-color) !important;
    }
  }
}

d-article d-contents {
  position: sticky;
  top: 2rem;

  a {
    font-weight: normal;
    color: grey !important;
  }

  a.active {
    color: black !important;
  }

  > nav {
    width: 252px;
    background-color: white;
  }
}

@media (max-width: 1290px) {
  d-article {
    d-contents {
      position: initial;
      display: block;
      grid-column: text;
      justify-self: start;
      grid-column-end: -2;
      padding-bottom: 0.5em;
      margin-bottom: 1em;
      padding-left: 0;
      padding-top: 0.5em;
      width: 100%;
      border-bottom: 1px solid var(--global-divider-color);
      nav {
        grid-column: none;
      }
      a {
        color: black !important;
      }
    }
  }
}

.page-content {
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.hiring-message {
  color: #333;
}
